.root{
  flex-grow: 1;
  margin-bottom: "7rem";
  display: "flex";
  height: 100px;
  
}
.appBar{
  padding: "0.5rem 0";
  display: "flex";
  height: "7rem";
  background-color: rgb(68, 151, 186) !important; /* Cambia el color de fondo del AppBar a rojo */

}

.navbar-buttons {
  margin-left: auto;
}

.navbar-buttons button {
  background-color: #4caf50; /* Color de fondo */
  color: white; /* Color del texto */
  border: none; /* Sin borde */
  padding: 10px 20px; /* Espaciado interno */
  border-radius: 5px; /* Bordes redondeados */
  cursor: pointer; /* Cambia el cursor al pasar por encima */
}

.navbar-buttons button:hover {
  background-color: #8646a3; /* Cambia el color de fondo al pasar por encima */
}
.logo-image {
  width: 100px;
  height: 100px;
  position: relative; 
}

.cart-icon{
  width: 40px;
  color: #121c18;
}

.navbar-buttons {
  display: flex; 
  align-items: center; 
  gap: 30px; 
}
.navbar-butons-options  {
  color: rgb(20, 5, 5); 
  padding: 10px 20px; 
  border-radius: 5px; 
  cursor: pointer; 
  margin-left: 60px;
}
@keyframes blink-scale {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.1);
    opacity: 0.7;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.blinking-scale {
  animation: blink-scale 1s infinite;
}

@media (max-width: 600px) {
  .navbar-butons-options {
    display: none;
  }
  .user-greeting {
    display: none;
  }
  .search-icon {
    display: none;
  }
  .logo-image {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 8px !important;
  }

  
}

.icon-menu{
  cursor: pointer;
}
@media (min-width: 600px) {
  .icon-menu
  {
    display: none;
  }
  .items-menu
  {
    display: none !important;
  }

}
/* Agrega estos estilos en tu archivo Navbar.css */

.items-menu {
  display: none;
  position: absolute;
  background-color: #fff;
  box-shadow: 0 8px 16px rgba(0,0,0,0.1);
  border-radius: 8px;
  padding: 16px;
  top: 70px; /* Ajusta la distancia desde la parte superior seg√∫n sea necesario */
  right: 52%;
  z-index: 1;
}

.items-menu a {
  display: block;
  padding: 8px 0;
  color: #333;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

.items-menu a:hover {
  background-color: #f4f4f4;
}
